.components-musiclistitem {
    height: 36px;
    line-height: 36px;
    border-bottom: 1px solid #ddd;
    text-indent: 10px;
    -webkit-transition: all .1s;
    -o-transition: all .1s;
    transition: all .1s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    background: #ddd;
}

.focus {
    color: #2f9842;
    background: #ddd;
}

.delete {
    cursor: pointer;
    height: 30px;
    width: 30px;
    top: 3px;
    position: relative;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    margin-right: 15px;
}

.delete:before, .delete:after {
    content: '';
    display: block;
    background: #666;
    position: absolute;
}

.delete:before {
    width: 100%;
    height: 1px;
    top: 14px;
    left: 0;
}

.delete:after {
    height: 100%;
    width: 1px;
    left: 15px;
    top: 0;
}

.components-musiclistitem:hover {
    background: #eee;
}

.components-musiclistitem:active {
    background: #ddd;
}



